import { getUserInfo, setUserInfo, removeToken, removeUserInfo } from '@/utils/token.js';
import { getUserInfo as fetchUserInfo } from '@/api/user.js';

export default {
  data() {
    return {
      userInfo: {}
    };
  },

  onLoad() {
