  },

  onShow() {
    // ÊØèÊ¨°ÊòæÁ§∫È°µÈù¢Êó∂ÈáçÊñ∞Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
    this.loadUserInfo();
  },

  methods: {
    // Ëé∑ÂèñÈ™ëÊâãËÆ§ËØÅÁä∂ÊÄÅÊñáÊú¨
    getRiderStatusText() {
      const { isRider, riderStatus } = this.userInfo;

      if (!isRider || isRider === 0) {
        return 'Êú™ËÆ§ËØÅ';
      }

      // Â∑≤Áî≥ËØ∑ËÆ§ËØÅÔºåÊ†πÊçÆÁä∂ÊÄÅÊòæÁ§∫
      switch (riderStatus) {
        case 0:
          return 'ÂÆ°Ê†∏‰∏≠';
        case 1:
          return 'Â∑≤ËÆ§ËØÅ';
        case 2:
          return 'Â∑≤È©≥Âõû';
        default:
          return 'Êú™ËÆ§ËØÅ';
      }
    },

    // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
    async loadUserInfo() {
      try {
        console.log('=== ÂºÄÂßãÂä†ËΩΩÁî®Êà∑‰ø°ÊÅØ ===');
        // ‰ªéÂêéÁ´ØËé∑ÂèñÊúÄÊñ∞ÁöÑÁî®Êà∑‰ø°ÊÅØ
        const result = await fetchUserInfo();

        console.log('APIËøîÂõûÁªìÊûú:', result);
        console.log('result.data:', result.data);

        if (result.data) {
          this.userInfo = result.data;

          // Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®
          setUserInfo(result.data);

          // ÊâìÂç∞ËØ¶ÁªÜÊó•ÂøóÁî®‰∫éË∞ÉËØï
          console.log('=== Áî®Êà∑‰ø°ÊÅØËØ¶ÊÉÖ ===');
          console.log('ÂÆåÊï¥Áî®Êà∑ÂØπË±°:', JSON.stringify(result.data, null, 2));
          console.log('isRiderÂÄº:', result.data.isRider, 'Á±ªÂûã:', typeof result.data.isRider);
          console.log('riderStatusÂÄº:', result.data.riderStatus, 'Á±ªÂûã:', typeof result.data.riderStatus);

          // Âà§Êñ≠ËÆ§ËØÅÁä∂ÊÄÅ
          const isCertified = result.data.isRider === 1 && result.data.riderStatus === 1;
          console.log('ÊòØÂê¶Â∑≤ËÆ§ËØÅ:', isCertified);
          console.log('ËÆ§ËØÅÁä∂ÊÄÅÊñáÊú¨:', this.getRiderStatusText());
        }
      } catch (error) {
        console.error('=== Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥• ===');
        console.error('ÈîôËØØ‰ø°ÊÅØ:', error);
        console.error('ÈîôËØØËØ¶ÊÉÖ:', JSON.stringify(error));

        // Â¶ÇÊûúAPIË∞ÉÁî®Â§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞ÁºìÂ≠òÁöÑÊï∞ÊçÆ
        const userInfo = getUserInfo();
        if (userInfo) {
          console.log('‰ΩøÁî®Êú¨Âú∞ÁºìÂ≠òÊï∞ÊçÆ:', userInfo);
          this.userInfo = userInfo;
        } else {
          // Êú™ÁôªÂΩïÔºåË∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µ
          console.log('Êú™ÁôªÂΩïÔºåË∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µ');
          uni.reLaunch({
            url: '/pages/login/login'
          });
        }
      }
    },

    // ÂàáÊç¢‰∏∫È™ëÊâãÊ®°Âºè
    switchToRiderMode() {
      // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÈÄöËøáÈ™ëÊâãËÆ§ËØÅÔºàisRider=1 ‰∏î riderStatus=1Ôºâ
      const isRiderCertified = this.userInfo.isRider === 1 && this.userInfo.riderStatus === 1;

      console.log('ÂΩìÂâçÁî®Êà∑‰ø°ÊÅØ:', this.userInfo);
      console.log('isRider:', this.userInfo.isRider);
      console.log('riderStatus:', this.userInfo.riderStatus);
      console.log('ÊòØÂê¶Â∑≤ËÆ§ËØÅ:', isRiderCertified);

      if (!isRiderCertified) {
        // Êú™ËÆ§ËØÅÊàñÂÆ°Ê†∏Êú™ÈÄöËøáÔºåÂºπÂá∫ÊèêÁ§∫
        uni.showModal({
          title: 'ÊèêÁ§∫',
          content: 'ÊÇ®ËøòÊú™ÈÄöËøáÈ™ëÊâãËÆ§ËØÅÔºåÊòØÂê¶ÂâçÂæÄËÆ§ËØÅÔºü',
          success: (res) => {
            if (res.confirm) {
              uni.navigateTo({
                url: '/pages/rider/auth'
              });
            }
          }
        });
        return;
      }

      // Â∑≤ËÆ§ËØÅÔºåËÆæÁΩÆÈ™ëÊâãÊ®°ÂºèÂπ∂Ë∑≥ËΩ¨Âà∞Êé•ÂçïÂ§ßÂéÖ
      uni.setStorageSync('currentMode', 2) // 2 = È™ëÊâãÊ®°Âºè

      uni.redirectTo({
        url: '/pages/hall/hall'
      });

      console.log('‚úÖ Â∑≤ÂàáÊç¢Âà∞È™ëÊâãÊ®°ÂºèÔºåcurrentMode = 2');
    },

    // ÈÄÄÂá∫ÁôªÂΩï
    handleLogout() {
      uni.showModal({
        title: 'ÈÄÄÂá∫ÁôªÂΩï',
        content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            // Ê∏ÖÈô§Êú¨Âú∞Â≠òÂÇ®
            removeToken();
            removeUserInfo();

            uni.showToast({
              title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
              icon: 'success'
