          console.error('‚ùå Èí±ÂåÖÈ°µÈù¢Ë∑≥ËΩ¨Â§±Ë¥•:', err);
          console.error('ÈîôËØØËØ¶ÊÉÖ:', JSON.stringify(err));

          uni.showModal({
            title: 'Ë∑≥ËΩ¨Â§±Ë¥•',
            content: 'Èí±ÂåÖÈ°µÈù¢Êú™ÊâæÂà∞ÔºåËØ∑ÈáçÊñ∞ÁºñËØëÈ°πÁõÆ„ÄÇ\n\nÊ≠•È™§Ôºö\n1. Âà†Èô§unpackageÊñá‰ª∂Â§π\n2. ÈáçÊñ∞ËøêË°åÈ°πÁõÆ',
            confirmText: 'ÊàëÁü•ÈÅì‰∫Ü',
